<template>
	<div class="jizhang">
		<div class="zdTitle">
			<div class="billDetail">
				<div class="bill" v-show="isShow">0	</div>
				<div class="bill" v-show="isTwoShow">{{ msg.totalAmt }}</div>
				<div class="also">近30天应还 <span></span>（元）</div>
			</div>
		</div>
		<div style="margin-top:2.4rem; background:#fff; height:0.91rem;"></div>
		<div class="billEg" v-show="isEgShow">
			<div class="title">
				<img class="headImg" src="./../images/address_logo.png">
				<span class="name">钱师爷</span>
				<span class="user">（田田）</span>
				<span class="details">详情</span>
			</div>
			<div class="billMoney">
				<div class="money">
					<span>50000.00</span> <br>
					<span>应还金额（元）</span>
				</div>
				<div class="days">
					<span>15（天）</span> <br>
					<span>剩余还款日</span>
				</div>
				<div></div>
			</div>
			<img class="eg" src="../images/eg.png" alt="">
		</div>
		<mt-cell-swipe v-for="(item,index) in msg.accountRecords" :right="[
			{ 
				content: '删除', style:{ background: '#ff4814', color: '#fff', fontSize: '0.28rem', letterSpacing: '0.028rem' }, handler() { remove(index) } 
			},
			{
				content: '已还', style:{ background: '#e62929', color: '#fff', fontSize: '0.28rem', letterSpacing: '0.028rem' }, handler() { updata(index) }
			}]" :key="item.id">
			<div class="title">
				<img class="headImg" :src="item.picUrl"></img>
				<span class="name">{{ item.pName }}</span>
				<span class="user">（{{ item.userName }}）</span>
				<span class="details" @click="showDetail(index)">详情</span>
			</div>
			<div class="billMoney">
				<div class="money">
					<span>{{ item.amtByMonth }}</span> <br>
					<span>应还金额（元）</span>
				</div>
				<div class="days">
					<span>{{ item.remainRepayDay }}</span> <br>
					<span>剩余还款日</span>
				</div>
				<div>
					<span class="current" v-show="item.repayStatus === 0 ? false : true">设为已还</span>
				</div>
			</div>
		</mt-cell-swipe>
		<div class="model" v-show="isShowThis">
			<div class="kuang">
				<div class="header">
					账单详情
				</div>
				<div class="detail">
					<div>当前期数 <span>{{ currentPeriods }}/{{ totalPeriods }} 期</span></div>
					<div>应还金额 <span>¥ {{ amtByMonth }}</span></div>
				</div>
				<div class="btn" @click="isShowModel">确定</div>
			</div>
		</div>
		<div class="addBtn" @click="next">添加账单</div>
	</div>
</template>
<script>
import { getStore } from '../config/mUtils'
import { records, deleteRecords, updataRecords } from './../api/api'
export default {
	data() {
		return {
			isEgShow: true,
			isSpanShow: false,
			msg: [],
			isShow: true,
			isTwoShow: false,
			isShowThis: false,
			totalPeriods: null, // 总期数
			currentPeriods: null, // 当前期数
			amtByMonth: null // 应还金额
		}
	},
	methods: {
		next() {	
			this.$router.push({path: '/jizhangList'})
		},
		showDetail(index) {
			this.currentPeriods = this.msg.accountRecords[index].currentPeriods
			this.totalPeriods = this.msg.accountRecords[index].totalPeriods
			this.amtByMonth = this.msg.accountRecords[index].amtByMonth
			this.isShowThis = true
		},
		isShowModel() {
			this.isShowThis = false
		},
		remove(index) {
			let params = { 
				id: this.msg.accountRecords[index].id
			}
			deleteRecords(params).then(res => {
				if (res.code === 200) {
					this.init()
				}
			})
		},
		updata(index) {
			if (this.msg.accountRecords[index].repayStatus === 0) {
				this.msg.accountRecords[index].repayStatus = 1
				let params = {
					id: this.msg.accountRecords[index].id,
					repayStatus: this.msg.accountRecords[index].repayStatus
				}
				updataRecords(params).then(res => {
					this.init()
				}) 
			} else {
				this.msg.accountRecords[index].repayStatus = 0
				let params = {
					id: this.msg.accountRecords[index].id,
					repayStatus: this.msg.accountRecords[index].repayStatus
				}
				updataRecords(params).then(res => {
					this.init()
				})
			}
		},
		init() {
			var userId = getStore('userId');
			if (userId !== null) {
				this.isEgShow = false
				let params = {
					userId: userId
				}
				records(params).then(res => {
					if (res.code === 200) {
						if (res.data.accountRecords.length > 0) {
							this.msg = res.data
							this.isShow = false
							this.isTwoShow = true
						} else {
							this.msg = []
							this.isEgShow = true
							this.isTwoShow = false
							this.isShow = true
						}
					}
				})
			}
		}
	},
	mounted() {
		this.init()
	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.jizhang {
		background: #f2f3f2;
		overflow: hidden;
		height: 100%;
		.model {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0,0,0,0.5);
			z-index: 111;
			.kuang {
				width: 5.27rem;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%);
				background: #fff;
				border-radius: 0.15rem;
			}
			.header {
				width: 100%;
				background: url(./../images/zhangdanDetail.png) no-repeat center;
				background-size: cover;
				height: 1rem;
				border-top-right-radius: 0.15rem;
				border-top-left-radius: 0.15rem;
				text-align: center;
				line-height: 1rem;
				color: #fff;
				font-size: 0.34rem;
				letter-spacing: 0.034rem;
			}
			.btn {
				width: 2.3rem;
				height: 0.56rem;
				line-height: 0.56rem;
				border: 0.01rem solid #d84949;
				margin: 0 auto;
				font-size: 0.22rem;
				color: #d84949;
				text-align: center;
				border-radius: 0.5rem;
				margin-bottom: 0.32rem;
			}
			.detail {
				display: flex;
				margin: 0.5rem 0;
				div {
					flex: 1;
					text-align: center;
					font-size: 0.28rem;
					color: #999;
					span {
						color: #333;
					}
				}
			}
		}
		.addBtn {
			width: 4.98rem;
			height: 1rem;
			text-align: center;
			line-height: 1rem;
			background-image: linear-gradient(0deg, #f36d47 1%, #d84949 100%);
			color: #fff;
			font-size: 0.32rem;
			margin: 0.92rem auto;
			border-radius: 0.5rem;
		}
		.mint-cell:nth-child(4) {
			margin-top: 0;
		}
		.mint-cell {
			margin-top: 0.1rem;
			.mint-cell-wrapper {
				padding: 0 0.35rem;
				display: block;
			}
			.mint-cell-swipe-buttongroup {
				display: flex;
				align-items: center;
				a {
					display: flex;
					align-items: center;
					line-height: normal;
				}
			}
			.mint-cell-value {
				background: #fff;
				position: relative;
				width: 100%;
				display: block;
				.title {
					padding-top: 0.1rem;
					padding-bottom: 0.1rem;
					border-bottom: 0.01rem solid #999;
				}
				span {
					display: inline-block;
					color: #333;
					font-size: 0.24rem;
					line-height: 0.3rem;
				}
				.headImg {
					width: 0.65rem;
					height: 0.65rem;
					vertical-align: middle;
					background: #000;
					margin-right: 0.14rem;
				}
				.details {
					float: right;
					margin-top: 0.15rem;
					color: #999;
				}
				.billMoney {
					padding: 0.04rem 0 0.1rem 0;
					display: flex;
					align-items: center;
					div {
						flex: 1;
						span {
							// margin-top: 0.1rem;
						}
						span:nth-child(3) {
							font-size: 0.24rem;
							color: #999;
						}
						.current {
							color: #fff;
							font-size: 0.22rem;
							padding: 0.11rem 0.1rem;
							border-radius: 0.15rem;
							background: #999;
						}
					}
					div:nth-child(1) {
						text-align: left;
					}
					div:nth-child(2) {
						text-align: center;
					}
					div:nth-child(3) {
						text-align: right;
					}
				}
			}
		}
	}
	.zdTitle {
		width: 100%;
		height: 2.57rem;
		background-image: linear-gradient(bottom, #f36d47 5%, #d84949 100%);
		background-image: -webkit-linear-gradient(bottom, #f36d47 5%, #d84949 100%);
		position: absolute;
		top: 0;
		z-index: 11;
		.billDetail {
			width: 5rem;
			height: 2.5rem;
			margin:  0.7rem auto 0;
			background: #fff;
			border-radius: 0.1rem;
			box-shadow: 0 0.05rem 0 0 #fdf6f6;
			.bill {
				color: #f95b55;
				font-size: 0.58rem;
				letter-spacing: 0.05rem;
				text-align: center;
				padding-top: 0.7rem;
			}
			.also {
				font-size: 0.24rem;
				letter-spacing: 0.02rem;
				color: #f95b55;
				text-align: center;
				padding-top: 0.2rem;
			}
		}
	}
	.billEg {
		padding: 0 0.35rem;
		background: #fff;
		position: relative;
		.title {
			padding-top: 0.2rem;
			padding-bottom: 0.2rem;
			border-bottom: 0.01rem solid #999;
		}
		span {
			display: inline-block;
			color: #333;
			font-size: 0.28rem;
		}
		.headImg {
			width: 0.65rem;
			height: 0.65rem;
			vertical-align: middle;
			margin-right: 0.14rem;
		}
		.details {
			float: right;
			margin-top: 0.15rem;
			color: #999;
		}
		.billMoney {
			padding: 0.04rem 0 0.1rem 0;
			display: flex;
			align-items: center;
			div {
				flex: 1;
				span {
					margin-top: 0.1rem;
				}
				span:nth-child(3) {
					font-size: 0.24rem;
					color: #999;
				}
			}
			div:nth-child(1) {
				text-align: left;
			}
			div:nth-child(2) {
				text-align: center;
			}
			div:nth-child(3) {
				text-align: right;
			}
		}
		.eg {
			width: 1.22rem;
			height: 0.97rem;
			position: absolute;
			top: 0.4rem;
			right: 1rem;
		}
	}
</style>



